import React from 'react';
import { Chart as ChartJS, ArcElement, Tooltip, Legend } from 'chart.js';
import { Doughnut } from 'react-chartjs-2';
import "./CalculatorExpensesResultsSectionDonutStyles.css"

function CalculatorExpensesResultsSectionDonut({formValues}) {

    let mortgagePayments = parseFloat(formValues.mortgagePayment).toFixed(2) || 0
    let otherExpenses = parseFloat(formValues.otherExpenses).toFixed(2) || 0
    let monthlyMaintenance = parseFloat(formValues.monthlyMaintenance).toFixed(2) || 0


    ChartJS.register(ArcElement, Tooltip, Legend);


    const calculatorData = {
        labels: ['Mortgage', 'Other', 'Maintenance'],
        datasets: [
            {
                data: [mortgagePayments, otherExpenses, monthlyMaintenance],

                backgroundColor: [
                    'rgba(22, 101, 216, 1)',
                    'rgba(1, 204, 116, 1)',
                    'rgba(255, 73, 152, 1)',
                ],
                borderColor: [
                    'rgba(22, 101, 216, 1)',
                    'rgba(1, 204, 116, 1)',
                    'rgba(255, 73, 152, 1)',
                ],
                borderWidth: 1,
            },
        ],
    }

    const options = {
        plugins: {
            tooltip: {
                backgroundColor: "#333333",
                displayColors: false,
                bodyAlign: "center",
                titleAlign: "center",
                callbacks: {
                    label: function(tooltipItem, data) {
                        return "$" + tooltipItem.formattedValue;
                    }}
            },
            legend: {
                display: false
            }
        },
    }



    return (
        <div className="calculator-results-section-wrapper">
            <h2 className="calculator-section-heading">
                Expenses
                <hr />
            </h2>
            <div className="calculator-results-donut-with-legend">
                <div className="calculator-results-legend">
                    <div className="calculator-results-legend-inner">
                        <h3>
                            <div className="color-swatch" style={{backgroundColor:'rgba(22, 101, 216, 1)'}}></div>Mortgage: <span>${parseFloat(formValues.mortgagePayment).toFixed(2) || 0 }</span></h3>
                        <h3>
                            <div className="color-swatch" style={{backgroundColor:'rgba(255, 203, 98, 1)'}}></div>Maintenance: <span>${parseFloat(formValues.monthlyMaintenance).toFixed(2) || 0 }</span></h3>
                        <h3>
                            <div className="color-swatch" style={{backgroundColor:'rgba(1, 204, 116, 1)'}}></div>Other Expenses: <span>${parseFloat(formValues.otherExpenses).toFixed(2) || 0}</span></h3>


                        <h3>
                            Total Expenses: <span>${(parseInt(mortgagePayments) + parseInt(monthlyMaintenance) + parseInt(otherExpenses)).toFixed(2)}</span>
                        </h3>
                    </div>


                </div>
                <Doughnut data={calculatorData} options={options} className="donut-results-chart"/>
            </div>
        </div>
    );
}

export default CalculatorExpensesResultsSectionDonut;