import React, {useEffect, useState} from 'react';
import "./IncomeComponentStyles.css"
import Paystub from "./Paystub/Paystub";
import InputLabel from '@mui/material/InputLabel';
import MenuItem from '@mui/material/MenuItem';
import FormControl from '@mui/material/FormControl';
import Select, {} from '@mui/material/Select';
import PlusButton from "./PlusButton.js";

function IncomeComponent({}) {

    const [incomeDetails, setIncomeDetails] = useState({
        paymentFrequency: 1,
        paystubs: [{startDate: "", endDate: "", grossEarnings: 0}],
        paystubAvg: 0,
        paystubTotal: 0,
    })

    const [validDates, setValidDates] = useState(0)

    const frequencies = [
        {label: "Weekly", divisor: 52},
        {label: "Bi-Weekly", divisor: 26},
        {label: "Bi-Monthly", divisor: 6},
        {label: "Monthly", divisor: 12},
        {label: "Quarterly", divisor: 4},]

    const handleSelect = (e) => {
        setIncomeDetails({
            ...incomeDetails,
            [e.target.name]: e.target.value
        })
    }

    const handleChange = (e, index) => {
        let tempPaystubs = incomeDetails.paystubs
        tempPaystubs[index][e.target.name] = e.target.value

        setValidDates(validateDates(tempPaystubs))


        setIncomeDetails({
            ...incomeDetails,
            paystubs: tempPaystubs
        })
    }

    const handleAddPaystub = () => {

        let tempPaystubs = incomeDetails.paystubs
        tempPaystubs.push({startDate: "", endDate: "", grossEarnings: 0})


        setIncomeDetails({
            ...incomeDetails,
            paystubs: tempPaystubs
        })
    }

    function validateDates(data) {
        for (let i = 0; i < data.length - 1; i++) {
            const currentEndDate = new Date(data[i].endDate);
            const currentStartDate = new Date(data[i].startDate)
            let nextEndDate = ""
            let nextStartDate = ""
            if (data.length > 1 && typeof (data[i + 1].startDate) !== 'undefined') {
                nextStartDate = new Date(data[i + 1].startDate);
            }

            if (data.length > 1 && typeof (data[i + 1].endDate) !== 'undefined') {
                nextEndDate = new Date(data[i + 1].endDate)
            }


            // Check for overlapping dates
            if (nextStartDate && currentEndDate >= nextStartDate) {

                return 1; // Error code for overlapping dates
            }

            if (currentEndDate <= currentStartDate) {

                return 1; // Error code for overlapping dates
            }

            if (nextStartDate >= nextEndDate) {
                return 1;
            }

            // Check for consecutive dates
            const expectedNextStartDate = new Date(currentEndDate);
            expectedNextStartDate.setDate(expectedNextStartDate.getDate() + 1);
            if (nextStartDate && nextStartDate.getTime() !== expectedNextStartDate.getTime()) {

                return 2; // Error code for dates that are not consecutive
            }
        }

        if (data.length === 1) {
            const currentEndDate = new Date(data[0].endDate);
            const currentStartDate = new Date(data[0].startDate)

            if (currentEndDate <= currentStartDate) {
                return 1; // Error code for overlapping dates
            }
        }

        return 0; // No errors found
    }

    useEffect(() => {
    }, [incomeDetails])

    function amount(item) {
        return item.grossEarnings
    }


    function sum(prev, next) {
        return parseFloat(prev) + parseFloat(next);
    }

    const totalEarnings = incomeDetails.paystubs.map(amount).reduce(sum);


    return (
        <div className="calculator-purchase-details-fields">
            <div className="select-component-wrapper">
                <h3>Select pay period length: </h3>
                <div className="select-component-styles">
                    <FormControl fullWidth>
                        <InputLabel id="demo-simple-select-label">Pay Period Length</InputLabel>
                        <Select
                            labelId="demo-simple-select-label"
                            id="demo-simple-select"
                            value={incomeDetails.paymentFrequency}
                            label="Payment Frequency"
                            name="paymentFrequency"
                            onChange={(e) => handleSelect(e)}
                        >
                            <MenuItem value={0}>Weekly</MenuItem>
                            <MenuItem value={1}>Bi-Weekly</MenuItem>
                            <MenuItem value={3}>Monthly</MenuItem>
                            <MenuItem value={2}>Bi-Monthly</MenuItem>
                            <MenuItem value={4}>Quarterly</MenuItem>
                        </Select>
                    </FormControl>
                </div>

            </div>
            <br/>


            <div className="paystubs-container">

                <h2 className="calculator-section-heading centered">Paystubs</h2>
                <div className="paystub-headers-container">
                    <h3>Paystub #</h3>
                    <h3>Start Date</h3>
                    <h3>End Date</h3>
                    <h3>Gross Earnings</h3>
                </div>
                {incomeDetails.paystubs.map((stub, index) => {
                    return (
                        <Paystub incomeDetails={incomeDetails} handleChange={handleChange}
                                 index={index}/>
                    )
                })}

                {validDates === 1 ?
                    <span className="error-span">Note: Paystub Dates Are Overlapping</span> : validDates === 2 ?
                        <span className="error-span">Note: Paystub Dates Should Be Consecutive</span> : ""}

                {incomeDetails.paystubs.length < 12 ? <PlusButton handleClick={handleAddPaystub}/> : <></>}


            </div>

            <div className="calculator-two-column-layout-plans">


                <div>
                    <h2 className="calculator-results-section-heading-plans">
                        Average:
                    </h2>
                    <h2 className="calculator-results-section-value-plans">
                        ${parseFloat((totalEarnings / incomeDetails.paystubs.length)).toLocaleString(undefined, {
                        minimumFractionDigits: 2,
                        maximumFractionDigits: 2
                    })}
                    </h2>
                </div>
                <div>
                    <h2 className="calculator-results-section-heading-plans">
                        Total:
                    </h2>
                    <h2 className="calculator-results-section-value-plans">
                        ${parseFloat((totalEarnings)).toLocaleString(undefined, {
                        minimumFractionDigits: 2,
                        maximumFractionDigits: 2
                    })}
                    </h2>
                </div>
                <div>
                    <h2 className="calculator-results-section-heading-plans">
                        Expected Annual Total:
                    </h2>
                    <h2 className="calculator-results-section-value-plans">
                        ${parseFloat((totalEarnings / incomeDetails.paystubs.length) * frequencies[incomeDetails.paymentFrequency].divisor).toLocaleString(undefined, {
                        minimumFractionDigits: 2,
                        maximumFractionDigits: 2
                    })}
                    </h2>
                </div>


            </div>


        </div>
    );
}

export default IncomeComponent;