import React from 'react';
import "./QuizQuestionAnswerChoiceStyles.css"

function QuizQuestionAnswerChoice({answer, adornment, setActiveAnswer, answerNumber, id}) {
    return (
        <div className="quizQuestionAnswerChoiceWrapper" id={id} onClick={() => setActiveAnswer(id)}>
            <div className="quizQuestionAnswerChoice">{adornment}</div>
            <span className="quizQuestionAnswerText">{answer}</span>
        </div>
    );
}

export default QuizQuestionAnswerChoice;