import React from 'react';
import "./ExpensesComponentStyles.css"
import TextFieldWithLabel from "../../TextFieldWithLabel/TextFieldWithLabel";

function ExpensesComponent({formValues, setFormValues, handleChange}) {

    return (
        <div className="calculator-purchase-details-fields">
            <h2 className="calculator-section-heading">
                Rental Property Tax Details
                <hr />
            </h2>
            <div className="calculator-two-column-layout">
                <TextFieldWithLabel label="Mortgage Payment" name="mortgagePayment" adornment="$" adornmentPosition="start" handleChange={handleChange} formValues={formValues} />
                <TextFieldWithLabel label="Monthly Maintenance Cost" name="monthlyMaintenance" adornment="$" adornmentPosition="start" handleChange={handleChange} formValues={formValues} />
                <TextFieldWithLabel label="Other Monthly Expenses" name="otherExpenses" adornment="$" adornmentPosition="start" handleChange={handleChange} formValues={formValues} />
                <TextFieldWithLabel label="Effective Tax Rate" name="taxRate" adornment="%" adornmentPosition="end" handleChange={handleChange} formValues={formValues} />
            </div>


        </div>
    );
}

export default ExpensesComponent;