import React, {useState} from 'react';
import "./PurchaseComponentDetailsStyles.css"
import OptionalFieldsTrigger from "../../OptionalFieldsTrigger/OptionalFieldsTrigger";
import TextFieldWithLabel from "../../TextFieldWithLabel/TextFieldWithLabel";
import SplitTextFieldWithLabel from "../../SplitTextFieldWIthLabel/SplitTextFieldWithLabel";
import InputLabel from "@mui/material/InputLabel";
import Select from "@mui/material/Select";
import MenuItem from "@mui/material/MenuItem";
import FormControl from "@mui/material/FormControl";
import {CSSDateField} from "../../CSSTextField/CSSTextField";

function PurchaseDetailsComponent({formValues, setFormValues, handleChange}) {


    return (
        <div className="calculator-purchase-details-fields">
            <h2 className="calculator-section-heading">
                Purchase Details
                <hr/>
            </h2>
            <FormControl fullWidth>
                <InputLabel id="demo-simple-select-label">Rental Property Type</InputLabel>
                <Select
                    labelId="demo-simple-select-label"
                    id="demo-simple-select"
                    label="Rental Property Type"
                    name="propertyType"
                    onChange={(e) => handleChange(e)}
                    value={formValues.propertyType}
                    required
                >
                    <MenuItem value={"residential"}>Residential</MenuItem>
                    <MenuItem value={"commercial"}>Commercial</MenuItem>
                </Select>
            </FormControl>
            <br/>
            <br/>
            <div className="calculator-two-column-layout">
                <TextFieldWithLabel label="Purchase Price" name="purchasePrice" adornment="$" adornmentPosition="start"
                                    handleChange={handleChange} formValues={formValues} value={formValues.purchasePrice}
                                    required={true}/>
                <TextFieldWithLabel label="Land Value" name="landValue" adornment="$" adornmentPosition="start"
                                    handleChange={handleChange} formValues={formValues} required={true}
                                    value={formValues.landValue}/>
                <div className="calculator-single-field-label center-last-child">
                    <h3 className="calculator-field-label">
                        Placed In Service Date
                    </h3>
                    <CSSDateField className="paystub-textfield" type="date"
                                  name="placedInService" onChange={(e) => handleChange(e)}
                                  value={formValues.placedInService}
                    />
                </div>

            </div>
        </div>
    );
}

export default PurchaseDetailsComponent;