import React, {useEffect, useState} from 'react';
import "./QuizResultStyles.css"
import ScheduleDemoButton from "../ScheduleDemoButton/ScheduleDemoButton";
import CSSTextField from "../../CSSTextField/CSSTextField";
import InputAdornment from "@mui/material/InputAdornment";


function QuizResult({result}) {
    const emailIcon = (<svg width="30" height="30" viewBox="0 0 131 131" fill="none" xmlns="http://www.w3.org/2000/svg">
        <path fill-rule="evenodd" clip-rule="evenodd"
              d="M73.4676 11.6237L123.462 42.2322C128.039 44.9884 130.881 50.0135 130.881 55.3533V106.385C130.881 114.827 124.013 121.695 115.571 121.695H15.5483C7.10657 121.695 0.238678 114.827 0.238678 106.385V55.3533C0.238678 50.0135 3.08065 44.9884 7.65771 42.2322L57.6517 11.6234L57.6905 11.5999C60.0625 10.1767 62.7835 9.42444 65.5597 9.42444C68.3358 9.42444 71.0568 10.1767 73.4288 11.6002L73.4676 11.6237ZM115.571 111.488C118.385 111.488 120.674 109.199 120.674 106.385V55.3533C120.674 53.5429 119.745 51.9048 118.189 50.9714C118.176 50.9632 118.163 50.9553 118.149 50.9474L68.1613 20.3425C67.3792 19.8768 66.48 19.6308 65.5597 19.6308C64.6393 19.6308 63.7401 19.8768 62.958 20.3425L12.9696 50.9474C12.9568 50.9555 12.9436 50.9633 12.9305 50.9711L12.9301 50.9714C11.3741 51.9048 10.4451 53.5429 10.4451 55.3533V106.385C10.4451 109.199 12.7344 111.488 15.5483 111.488H115.571ZM23.1293 67.5511L57.6907 88.9003C62.5273 91.8025 68.5978 91.7992 73.4287 88.9L107.99 67.5508C110.407 66.1007 111.191 62.9661 109.741 60.5492C108.291 58.1321 105.156 57.3487 102.739 58.7988L68.1773 80.1483C66.5741 81.1103 64.5433 81.109 62.9422 80.1483L28.3805 58.7991C25.9646 57.3492 22.8292 58.1328 21.3789 60.5495C19.9288 62.9663 20.7124 66.101 23.1293 67.5511Z"
              fill="#667085"/>
    </svg>)

    const [emailAddress, setEmailAddress] = useState("")


    const handleSubmit = (e) => {
        e.preventDefault()

        const encodedEmail = encodeURIComponent(emailAddress).replace(/%40/g, "@");
        window.parent.postMessage({action: "submittedQuizDemo", email: encodedEmail}, "*");
    }


    return (
        <div className="quizResultWrapper">

            <h2 className="quiz-results-title-text">{result.mainTitle}</h2>
            <h3>{result.bulletPoint1}</h3>
            <h3>{result.bulletPoint2}</h3>
            <h3>{result.bulletPoint3}</h3>
            <h2 className="quiz-results-title-text">Ready To Learn More?</h2>
            <form className="quiz-results-form" onSubmit={(e) => handleSubmit(e)}>
                <CSSTextField className="quiz-demo-email" value={emailAddress}
                              onChange={(e) => setEmailAddress(e.target.value)} InputProps={{
                    startAdornment: <InputAdornment position="start"><span
                        className="email-form-icon">{emailIcon}</span></InputAdornment>
                }}/>

                <ScheduleDemoButton emailAddress={emailAddress}/>
            </form>

        </div>
    );
}

export default QuizResult;