import React, {useEffect, useState} from 'react';
import "./IncomeComponentStyles.css"
import TextFieldWithLabel from "../../TextFieldWithLabel/TextFieldWithLabel";
import InputLabel from '@mui/material/InputLabel';
import MenuItem from '@mui/material/MenuItem';
import FormControl from '@mui/material/FormControl';
import Select, {} from '@mui/material/Select';

function IncomeComponent({formValues, setFormValues, handleChange}) {

    const [incomeDetails, setIncomeDetails] = useState({
        paymentFrequency: 0,
        weeklyHours: 40,
        yearlyWeeks: 52,
        yearlyMonths: 12,
        yearlyQuarters: 4,
        basePay: 0,
        extraIncome: 0
    })

    const frequencies = [{label: "Hourly", divisor: incomeDetails.weeklyHours * incomeDetails.yearlyWeeks},
        {label: "Weekly", divisor: incomeDetails.yearlyWeeks},
        {label: "Bi-Weekly", divisor: incomeDetails.yearlyWeeks / 2},
        {label: "Bi-Monthly", divisor: incomeDetails.yearlyMonths / 2},
        {label: "Monthly", divisor: incomeDetails.yearlyMonths},
        {label: "Quarterly", divisor: incomeDetails.yearlyQuarters},
        {label: "Yearly", divisor: 1},]

    const handleSelect = (e) => {
        setIncomeDetails({
            ...incomeDetails,
            [e.target.name]: e.target.value
        })
    }

    useEffect(() => {
    }, [incomeDetails])


    return (
        <div className="calculator-purchase-details-fields">
            <div className="select-component-wrapper">
                <h3>Select payment frequency: </h3>
                <div className="select-component-styles">
                    <FormControl fullWidth>
                        <InputLabel id="demo-simple-select-label">Pay Frequency</InputLabel>
                        <Select
                            labelId="demo-simple-select-label"
                            id="demo-simple-select"
                            value={incomeDetails.paymentFrequency}
                            label="Payment Frequency"
                            name="paymentFrequency"
                            onChange={(e) => handleSelect(e)}
                        >
                            <MenuItem value={0}>Hourly</MenuItem>
                            <MenuItem value={1}>Weekly</MenuItem>
                            <MenuItem value={2}>Bi-Weekly</MenuItem>
                            <MenuItem value={4}>Monthly</MenuItem>
                            <MenuItem value={3}>Bi-Monthly</MenuItem>
                            <MenuItem value={5}>Quarterly</MenuItem>
                            <MenuItem value={6}>Yearly</MenuItem>
                        </Select>
                    </FormControl>
                </div>

            </div>
            <br/>

            <div className="calculator-two-column-layout">
                <TextFieldWithLabel label={frequencies[incomeDetails.paymentFrequency].label + " Pay"} name="basePay"
                                    adornment="$" adornmentPosition="start" handleChange={(e) => handleSelect(e)}
                                    formValues={incomeDetails}/>
                <TextFieldWithLabel label={frequencies[incomeDetails.paymentFrequency].label + " Tips/Commission"}
                                    name="extraIncome" adornment="$" adornmentPosition="start"
                                    handleChange={(e) => handleSelect(e)} formValues={incomeDetails}/>

                {incomeDetails.paymentFrequency === 0 ? <><TextFieldWithLabel label="# Of Hours Worked Weekly"
                                                                              name="weeklyHours" adornment="Hours"
                                                                              adornmentPosition="end"
                                                                              handleChange={(e) => handleSelect(e)}
                                                                              formValues={incomeDetails}/>
                        <TextFieldWithLabel label="# Of Weeks Worked Yearly" name="yearlyWeeks" adornment="Weeks"
                                            adornmentPosition="end" handleChange={(e) => handleSelect(e)}
                                            formValues={incomeDetails}/></>
                    : incomeDetails.paymentFrequency === 1 || incomeDetails.paymentFrequency === 2 ?
                        <TextFieldWithLabel label="# Of Weeks Worked Yearly" name="yearlyWeeks" adornment="Weeks"
                                            adornmentPosition="end" handleChange={(e) => handleSelect(e)}
                                            formValues={incomeDetails}/>


                        : incomeDetails.paymentFrequency === 3 || incomeDetails.paymentFrequency === 4 ?
                            <TextFieldWithLabel label="# Of Months Worked Yearly" name="yearlyMonths" adornment="Months"
                                                adornmentPosition="end" handleChange={(e) => handleSelect(e)}
                                                formValues={incomeDetails}/> : incomeDetails.paymentFrequency === 5 ?
                                <TextFieldWithLabel label="# Of Quarters Worked Yearly" name="yearlyQuarters"
                                                    adornment="Quarters" adornmentPosition="end"
                                                    handleChange={(e) => handleSelect(e)} formValues={incomeDetails}/> :


                                incomeDetails.paymentFrequency === 6 ? <></> : <></>}


            </div>

            <div className="calculator-two-column-layout-plans">


                <div>
                    <h2 className="calculator-results-section-heading-plans">
                        Subtotal:
                    </h2>
                    <h2 className="calculator-results-section-value-plans">
                        ${(incomeDetails.basePay * frequencies[incomeDetails.paymentFrequency].divisor).toLocaleString()}
                    </h2>
                </div>
                <div>
                    <h2 className="calculator-results-section-heading-plans">
                        Total Tips:
                    </h2>
                    <h2 className="calculator-results-section-value-plans">
                        ${(incomeDetails.extraIncome * frequencies[incomeDetails.paymentFrequency].divisor).toLocaleString()}
                    </h2>
                </div>
                <div>
                    <h2 className="calculator-results-section-heading-plans">
                        Annual Total:
                    </h2>
                    <h2 className="calculator-results-section-value-plans">
                        ${((incomeDetails.extraIncome * frequencies[incomeDetails.paymentFrequency].divisor) + (incomeDetails.basePay * frequencies[incomeDetails.paymentFrequency].divisor)).toLocaleString()}
                    </h2>
                </div>


            </div>


        </div>
    );
}

export default IncomeComponent;