import React, {useEffect} from 'react';
import "./ScheduleDemoButtonStyles.css"

function ScheduleDemoButton({emailAddress}) {

    function isValidEmail(email) {
        const emailRegex = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
        return emailRegex.test(String(email).toLowerCase());
    }

    useEffect(() => {
        if (isValidEmail(emailAddress)) {
            document.getElementById("schedule-button-id").classList.add("schedule-button-active")
        } else {
            document.getElementById("schedule-button-id").classList.remove("schedule-button-active")
        }


    }, [emailAddress])


    return (
        <div className="schedule-button-wrapper">
            <button
                className="schedule-button"
                id="schedule-button-id"
            >
                <span className="schedule-button-text">Schedule A Demo</span>
            </button>
        </div>

    );
}

export default ScheduleDemoButton;