import './App.css';
import ROICalculator from "./components/ROICalculator/ROICalculator";
import {BrowserRouter as Router, Route, Routes} from "react-router-dom";
import RITCalculator from "./components/RITCalculator/RITCalculator";
import PricingComparison from "./components/PricingComparison/PricingComparison";
import PMSoftwareQuiz from "./components/Quizzes/PMSoftwareQuiz/PMSoftwareQuiz";
import IncomeVerificationCalculator from "./components/IncomeVerificationCalculator/IncomeVerificationCalculator";
import AdvancedIncomeVerificationCalculator
    from "./components/AdvancedIncomeVerificationCalculator/AdvancedIncomeVerificationCalculator";
import Directory from "./components/Directory/Directory";
import DepreciationCalculator from "./components/DepreciationCalculator/DepreciationCalculator";
import MortgageCalculator from "./components/MortgageCalculator/MortgageCalculator";
import CapRateCalculator from "./components/CapRateCalculator/CapRateCalculator";
import RealEstateChatbot from "./components/RealEstateChatbot/RealEstateChatbot";
import TestJobBoard from "./components/TestJobBoard/TestJobBoard";


function App() {

    const pmSoftwareJsonQuestions = require("./components/Quizzes/QuestionText/PMSoftwareQuizQuestions.json")
    const qbSoftwareJsonQuestions = require("./components/Quizzes/QuestionText/QBSoftwareQuizQuestions.json")
    const generalSoftwareJsonQuestions = require("./components/Quizzes/QuestionText/generalSoftwareQuizQuestions.json")
    const softwareComparisonQuestions = require("./components/Quizzes/QuestionText/softwareComparisonQuestions.json")


    return (
        <div className="app">
            <Router basename="/website-content">
                <Routes>
                    <Route path="/" element={<Directory/>}/>
                    <Route path="/return-on-investment" element={<ROICalculator/>}/>
                    <Route path="/rental-income-tax" element={<RITCalculator/>}/>
                    <Route path="/property-depreciation" element={<DepreciationCalculator/>}/>
                    <Route path="/pricing-comparison" element={<PricingComparison/>}/>
                    <Route path="/pm-software-quiz"
                           element={<PMSoftwareQuiz jsonQuestions={pmSoftwareJsonQuestions}/>}/>
                    <Route path="/qb-software-quiz"
                           element={<PMSoftwareQuiz jsonQuestions={qbSoftwareJsonQuestions}/>}/>
                    <Route path="/general-software-quiz"
                           element={<PMSoftwareQuiz jsonQuestions={generalSoftwareJsonQuestions}/>}/>
                    <Route path="/software-comparison-quiz/:software"
                           element={<PMSoftwareQuiz jsonQuestions={softwareComparisonQuestions}/>}/>
                    <Route path="/income-verification" element={<IncomeVerificationCalculator/>}/>
                    <Route path="/income-verification-advanced" element={<AdvancedIncomeVerificationCalculator/>}/>
                    <Route path="/mortgage-calculator" element={<MortgageCalculator/>}/>
                    <Route path="/cap-rate-calculator" element={<CapRateCalculator/>}/>
                    <Route path="/real-estate-chatbot" element={<RealEstateChatbot/>}/>
                    <Route path="/youtube-video/:id" element={<TestJobBoard/>}/>
                </Routes>
            </Router>
        </div>
    );
}

export default App;
