import React, {useState} from 'react';
import {styled} from '@mui/material/styles';
import Tabs from '@mui/material/Tabs';
import Tab from '@mui/material/Tab';
import Box from '@mui/material/Box';
import "./PlansTabsComponentStyles.css"
import StarterPlanPricingSection from "./PlanPrices/StarterPlanPricingSection";
import ProPlanPricingSection from "./PlanPrices/ProPlanPricingSection";
import PremiumPlanPricingSection from "./PlanPrices/PremiumPlanPricingSection";


const StyledTabs = styled((props) => (
    <Tabs
        {...props}
        TabIndicatorProps={{children: <span className="MuiTabs-indicatorSpan"/>}}
    />
))({
    '& .MuiTabs-indicator': {
        display: 'flex',
        justifyContent: 'center',
        backgroundColor: 'transparent',
        padding: 0
    },
    '& .MuiTabs-indicatorSpan': {
        maxWidth: 40,
        width: '100%',
        backgroundColor: '#1665D8'
    },
});

export default function PlansTabsComponent({formValues}) {
    const [value, setValue] = React.useState(1);
    const handleChange = (event, newValue) => {
        setValue(newValue);
    };

    return (
        <Box>
            <StyledTabs
                value={value}
                onChange={handleChange}
            >
                {formValues.numOfUnits <= 160 ? <StyledTab label="Starter"/> :
                    <StyledTab label="Starter" className="disabled" disabled/>}
                {formValues.numOfUnits >= 160 && value === 0 ? setValue(1) : ""}

                <StyledTab label="Pro"/>
                <StyledTab label="Premium"/>
            </StyledTabs>
            <Box sx={{padding: 2}}>
                {value === 0 && (
                    <Box>
                        <StarterPlanPricingSection formValues={formValues}/>
                    </Box>
                )}
                {value === 1 && (
                    <Box>
                        <ProPlanPricingSection formValues={formValues}/>
                    </Box>
                )}
                {value === 2 && (
                    <Box>
                        <PremiumPlanPricingSection formValues={formValues}/>
                    </Box>
                )}
            </Box>
        </Box>
    );
}

const StyledTab = styled((props) => <Tab disableRipple {...props} />)(
    ({theme}) => ({
        textTransform: 'none',
        fontSize: "25px",
        width: "180px",
        marginRight: theme.spacing(1),
        color: '#344054',
        fontWeight: "600",
        padding: "0px 15px 0px 15px",
        fontFamily: "'Poppins', sans-serif",
        '&.Mui-selected': {
            color: '#1665D8'
        },
        '&.Mui-focusVisible': {
            backgroundColor: 'rgba(100, 95, 228, 0.32)',
        },
    }),
);
