import React from 'react';
import "./PaystubStyles.css"
import CSSDateField from "../../../CSSTextField/CSSTextField";
import InputAdornment from "@mui/material/InputAdornment";
import CssTextField from "../../../CSSTextField/CSSTextField";

function Paystub({incomeDetails, handleChange, index}) {


    return (
        <div className="paystub-wrapper">
            <h3>Paystub #{index + 1}</h3>
            <CSSDateField className="paystub-textfield" type="date" value={incomeDetails.paystubs[index].startDate}
                          name="startDate"
                          onChange={(e) => handleChange(e, index)}/>
            <CSSDateField className="paystub-textfield" type="date" value={incomeDetails.paystubs[index].endDate}
                          name="endDate"
                          onChange={(e) => handleChange(e, index)}/>
            <CssTextField className="paystub-textfield" id="outlined-basic" variant="outlined" type="number"
                          onWheel={(e) => e.target.blur()}
                          value={incomeDetails.paystubs[index].grossEarnings || NaN}
                          name="grossEarnings"
                          onChange={(e) => handleChange(e, index)}
                          InputProps={{startAdornment: <InputAdornment position="start">$</InputAdornment>}}/>

        </div>
    );
}

export default Paystub;