import React, {useState} from 'react';
import "./DepreciationComponentStyles.css"
import TextFieldWithLabel from "../../TextFieldWithLabel/TextFieldWithLabel";
import SplitTextFieldWithLabel from "../../SplitTextFieldWIthLabel/SplitTextFieldWithLabel";

function DepreciationComponent({formValues, setFormValues, handleChange}) {

    return (
        <div className="calculator-purchase-details-fields">
            <h2 className="calculator-section-heading">
                Depreciation Details
                <hr />
            </h2>
            <div className="calculator-two-column-layout">
                <TextFieldWithLabel label="Purchase Price" name="purchasePrice" adornment="$" adornmentPosition="start" handleChange={handleChange} formValues={formValues} required={true}/>
                <SplitTextFieldWithLabel label="Estimated Land Value" name1="landValueAmount" name2="landValuePercent" adornment="$" adornmentPosition="start" adornmentSecondary="%" adornmentPositionSecondary="end"  handleChange={handleChange} formValues={formValues} required={true}/>
            </div>


        </div>
    );
}

export default DepreciationComponent;