import React, {useEffect, useState} from 'react';
import "./CalculatorResultsStyles.css"


function CalculatorResults({depreciation}) {
    function createOutputTable(depreciationSchedule) {
        const table = document.createElement("table");
        const headerRow = document.createElement("tr");

        ["Year", "Annual Depreciation", "Accumulated Depreciation", "Remaining Depreciable Basis"].forEach(headerText => {
            const headerCell = document.createElement("th");
            headerCell.textContent = headerText;
            headerRow.appendChild(headerCell);
        });

        table.appendChild(headerRow);

        depreciationSchedule.forEach(year => {
            const row = document.createElement("tr");

            for (const key in year) {
                const cell = document.createElement("td");
                cell.textContent = year[key].toFixed(2);
                row.appendChild(cell);
            }

            table.appendChild(row);
        });
        return table;
    }

    createOutputTable(depreciation)

    // document.querySelector(".calculator-results-wrapper").append(table)

    return (
        <div className="calculator-results-wrapper">
            <h1 className="centered heading-margin-bottom">
                Depreciation Results
            </h1>
            <table>
                <tr>
                    {["Year", "Annual Depreciation", "Accumulated Depreciation", "Remaining Depreciable Basis"].map((headerText) => {
                        return (<th>{headerText}</th>)
                    })
                    }
                </tr>
                {depreciation.map((year) => {
                    return (<tr>
                        {Object.keys(year).map((key) => {
                            return key === "year" ? (<td>{year[key].toLocaleString()}</td>) : (
                                <td>${year[key].toLocaleString()}</td>)
                        })}
                    </tr>)
                })}
            </table>
        </div>

    );
}

export default CalculatorResults;

