import React, {useState} from 'react';
import "./RealEstateChatbotStyles.css"

function RealEstateChatbot(props) {
    const [input, setInput] = useState('');
    const [messages, setMessages] = useState([{role: "system", content: "You are a helpful real estate chatbot"}]);

    const handleSubmit = async (event) => {
        event.preventDefault();
        setMessages(messages => [...messages, {
            role: "user",
            content: input
        }]);

        // The conversation history should include both user and bot messages
        const conversationHistory = [...messages, {role: "user", content: input}]
        setInput('');

        // We'll include the system message directly in the prompt
        // Make a GET request to the server
        try {

            const result = await fetch('https://login-scraping-project.vercel.app/generate-text?prompt=' + JSON.stringify(conversationHistory), {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/json'
                },
            });

            const resultData = await result.text();
            //console.log(JSON.parse(resultData).text)

            setMessages(messages => [...messages, {role: "system", content: resultData}]);


        } catch (e) {
            console.log(e)
        }
    };

    // Rest of your code...

    const chatStyles = {
        display: 'flex',
        flexDirection: 'column',
        padding: '10px',
        maxHeight: '500px',
        overflowY: 'scroll',
        border: '1px solid #ddd',
        borderRadius: '5px',
        width: '300px',
        margin: '0 auto',
    };

    const userMessageStyles = {
        alignSelf: 'flex-end',
        backgroundColor: '#0084FF',
        color: 'white',
        borderRadius: '20px',
        padding: '10px',
        margin: '5px',
    };

    const botMessageStyles = {
        alignSelf: 'flex-start',
        backgroundColor: '#f0f0f0',
        color: 'black',
        borderRadius: '20px',
        padding: '10px',
        margin: '5px',
    };

    return (
        <div>

            <div style={chatStyles}>
                {messages.slice(1).map((message, index) => (
                    <div key={index} style={message.role === 'user' ? userMessageStyles : botMessageStyles}>
                        {message.content}
                    </div>
                ))}
            </div>
            <form onSubmit={handleSubmit}>
                <input type="text" value={input} onChange={e => setInput(e.target.value)}
                       placeholder="Type a message..."/>
                <input type="submit" value="Send"/>
            </form>
        </div>
    );
}

export default RealEstateChatbot;