import React, {useEffect, useState} from 'react';
import {useParams} from 'react-router';
import "./PMSoftwareQuizStyles.css"
import QuizQuestion from "../QuizQuestion/QuizQuestion";
import QuizResult from "../QuizResult/QuizResult";

function PmSoftwareQuiz({jsonQuestions}) {

    const [activeQuestionNumber, setActionQuestionNumber] = useState(0);
    const [selectedAnswers, setSelectedAnswers] = useState([])
    const params = useParams();

    if (params.software) {
        let tempJson = JSON.stringify(jsonQuestions)
        tempJson = tempJson.replaceAll("=+=SOFTWARE=+=", params.software)
        jsonQuestions = JSON.parse(tempJson)
    }

    return (
        <div className="pmSoftwareQuizWrapper">
            <img
                src="https://global-uploads.webflow.com/5f073e32d304276cc8b4ff30/5fbc3a52ef4b4f37559b9b45_Blue%20Logo%20-%20Left%20Emblem.svg"
                className="quiz-result-header-image"/>
            {activeQuestionNumber < jsonQuestions.questions.length ?
                <QuizQuestion question={jsonQuestions.questions[activeQuestionNumber]}
                              activeQuestionNumber={activeQuestionNumber}
                              setActionQuestionNumber={setActionQuestionNumber} selectedAnswers={selectedAnswers}
                              setSelectedAnswers={setSelectedAnswers}/> :
                <QuizResult result={jsonQuestions["result" + selectedAnswers[0]][Math.floor(Math.random() * 3)]}/>
            }
        </div>
    );
}

export default PmSoftwareQuiz;