import React, {useState} from 'react';
import "./RITCalculator.css"
import DepreciationComponent from "./DepreciationComponent/DepreciationComponent";
import IncomeComponent from "./IncomeComponent/IncomeComponent";
import CalculateButton from "../CalculateButton/CalculateButton";
import CalculatorResults from "./CalculatorResults/CalculatorResults";
import ExpensesComponent from "./ExpensesComponent/ExpensesComponent";


function RitCalculator(props) {


    const [showResults, setShowResults] = useState(false)
    const [mortgagePayments, setMortgagePayments] = useState(0)
    const [formValues, setFormValues] = useState({
        monthlyIncome: "",
        monthlyRent: "",
        landValuePercent: "",
        landValueAmount: "",
        purchasePrice: "",
        taxRate: "",
        otherExpenses: "",
        monthlyMaintenance: "",
        mortgagePayment: "",
    })

    const handleChange = (evt) => {
        evt.preventDefault()
        const value = evt.target.value;
        let landValueField = ""
        let landValueValue = 0

        if(evt.target.name === "landValueAmount"){
            landValueField = "landValuePercent";
            landValueValue = (value / (formValues.purchasePrice ?? 0)) * 100
        }else if (evt.target.name === "landValuePercent"){
            let percentDecimal = value / 100;
            landValueField = "landValueAmount";
            landValueValue = (formValues.purchasePrice ?? 0) * percentDecimal
        }

        setFormValues({
            ...formValues,
            [evt.target.name]: parseFloat(value),
            [landValueField]: landValueValue
        });
    }

    const handleSubmit = (e) => {
        e.preventDefault();

        let temp = formValues

        for(let property in formValues){
            if(temp[property] === ""){
                temp[property] = 0
            }
        }

        setFormValues(temp)
        setShowResults(true)
    }



    return (
        <div className="calculator-wrapper">
            {!showResults ?

                <div className="calculator-body">
                <form onSubmit={(e) => handleSubmit(e)}>
                        <DepreciationComponent formValues={formValues} setFormValues={setFormValues} handleChange={handleChange}/>
                        <IncomeComponent formValues={formValues} setFormValues={setFormValues} handleChange={handleChange}/>
                    <ExpensesComponent formValues={formValues} setFormValues={setFormValues} handleChange={handleChange}/>
                        <CalculateButton />
                </form>
            </div>

                :

                <CalculatorResults formValues={formValues} mortgagePayments={mortgagePayments} />


            }

        </div>

    );
}

export default RitCalculator;