import React, {useState} from 'react';
import "./DepreciationCalculator.css"
import PurchaseDetailsComponent from "./PurchaseDetailsComponent/PurchaseDetailsComponent";
import ExpectedIncomeComponent from "./ExpectedIncomeComponent/ExpectedIncomeComponent";
import CalculateButton from "../CalculateButton/CalculateButton";
import CalculatorResults from "./CalculatorResults/CalculatorResults";


function DepreciationCalculator(props) {


    const [showResults, setShowResults] = useState(false)
    const [mortgagePayments, setMortgagePayments] = useState(0)
    const [table, setTable] = useState("")
    const [formValues, setFormValues] = useState({
        purchasePrice: "",
        landValue: "",
        placedInService: "",
        propertyType: "",
    })
    const [depreciationSchedule, setDepreciationSchedule] = useState([])

    const handleChange = (evt) => {
        evt.preventDefault()
        const value = evt.target.value;

        setFormValues({
            ...formValues,
            [evt.target.name]: value,
        });
    }

    const handleSubmit = (e) => {
        e.preventDefault();

        const depreciationScheduleCalc = calculateDepreciation(formValues.propertyType, formValues.purchasePrice, formValues.landValue, formValues.placedInService);
        setDepreciationSchedule(depreciationScheduleCalc)

        setShowResults(true)

    }

    function calculateDepreciation(propertyType, propertyCost, landValue, placeInServiceDate) {
        const depreciableBasis = propertyCost - landValue;
        const recoveryPeriod = propertyType === "residential" ? 27.5 : 39;
        const annualDepreciationRates = getMACRSRates(recoveryPeriod);
        const depreciationSchedule = [];

        for (let i = 0; i < recoveryPeriod; i++) {
            const annualDepreciation = depreciableBasis * annualDepreciationRates[i];
            const accumulatedDepreciation = depreciationSchedule.reduce((sum, year) => sum + year.annualDepreciation, 0) + annualDepreciation;
            const remainingDepreciableBasis = depreciableBasis - accumulatedDepreciation;

            if (annualDepreciation && accumulatedDepreciation && remainingDepreciableBasis) {
                depreciationSchedule.push({
                    year: i + 1,
                    annualDepreciation: annualDepreciation,
                    accumulatedDepreciation: accumulatedDepreciation,
                    remainingDepreciableBasis: remainingDepreciableBasis
                });
            }

        }

        return depreciationSchedule;
    }

    function getMACRSRates(recoveryPeriod) {
        // Add the relevant MACRS depreciation rates for the specified recovery period.
        // For simplicity, this example assumes mid-year convention.
        // You should adjust these rates according to the specific convention and property type in your jurisdiction.
        if (recoveryPeriod === 27.5) {
            return [
                0.036364, 0.035484, 0.034605, 0.033727, 0.032848, 0.031969, 0.031090, 0.030211, 0.029332, 0.028454,
                0.027575, 0.026696, 0.025817, 0.024938, 0.024060, 0.023181, 0.022302, 0.021423, 0.020544, 0.019665,
                0.018787, 0.017908, 0.017029, 0.016150, 0.015271, 0.015271, 0.015271
            ];
        } else {
            return Array(39).fill(1 / 39);
        }
    }


    return (
        <div className="calculator-wrapper">
            {!showResults ?

                <div className="calculator-body">
                    <form onSubmit={(e) => handleSubmit(e)}>
                        <PurchaseDetailsComponent formValues={formValues} setFormValues={setFormValues}
                                                  handleChange={handleChange}/>
                        <CalculateButton/>
                    </form>
                </div>

                :

                <CalculatorResults depreciation={depreciationSchedule}/>


            }

        </div>

        // <div className="calculator-wrapper">
        //
        //
        //         <div className="calculator-body">
        //             <form onSubmit={(e) => handleSubmit(e)}>
        //                 <DepreciationComponent formValues={formValues} setFormValues={setFormValues} handleChange={handleChange}/>
        //                 <IncomeComponent formValues={formValues} setFormValues={setFormValues} handleChange={handleChange}/>
        //                 <ScheduleDemoButton />
        //             </form>
        //         </div>
        //
        //
        //
        //         <CalculatorResults formValues={formValues} mortgagePayments={mortgagePayments} />
        //
        //
        //
        //
        // </div>
    );
}

export default DepreciationCalculator;