import React, {useState} from 'react';
import "./PricingComparisonStyles.css"
import TextFieldWithLabel from "../TextFieldWithLabel/TextFieldWithLabel";
import PlansTabsComponent from "./PlansTabsComponent/PlansTabsComponent";
import Switch from '@mui/material/Switch';
import {styled} from '@mui/material/styles';

const IOSSwitch = styled((props) => (
    <Switch focusVisibleClassName=".Mui-focusVisible" disableRipple {...props} />
))(({theme}) => ({
    width: 42,
    height: 26,
    padding: 0,
    '& .MuiSwitch-switchBase': {
        padding: 0,
        margin: 2,
        transitionDuration: '300ms',
        '&.Mui-checked': {
            transform: 'translateX(16px)',
            color: '#fff',
            '& + .MuiSwitch-track': {
                backgroundColor: theme.palette.mode === 'dark' ? '#1665d8' : '#1665d8',
                opacity: 1,
                border: 0,
            },
            '&.Mui-disabled + .MuiSwitch-track': {
                opacity: 0.5,
            },
        },
        '&.Mui-focusVisible .MuiSwitch-thumb': {
            color: '#33cf4d',
            border: '6px solid #fff',
        },
        '&.Mui-disabled .MuiSwitch-thumb': {
            color:
                theme.palette.mode === 'light'
                    ? theme.palette.grey[100]
                    : theme.palette.grey[600],
        },
        '&.Mui-disabled + .MuiSwitch-track': {
            opacity: theme.palette.mode === 'light' ? 0.7 : 0.3,
        },
    },
    '& .MuiSwitch-thumb': {
        boxSizing: 'border-box',
        width: 22,
        height: 22,
    },
    '& .MuiSwitch-track': {
        borderRadius: 26 / 2,
        backgroundColor: theme.palette.mode === 'light' ? '#E9E9EA' : '#39393D',
        opacity: 1,
        transition: theme.transitions.create(['background-color'], {
            duration: 500,
        }),
    },
}));

function PricingComparison(props) {

    const [formValues, setFormValues] = useState({
        numOfUnits: 25,
        chosenPlan: 2,
        numOfWebsites: 1,
        merchantApplications: 4,
        eSignatures: 3,
        annualOnboardings: 1,
        monthlyYearlyCheck: "on",

    })

    const getBackgroundSize = () => {
        return {
            backgroundSize: `${(formValues.numOfUnits * 100) / 5000}% 100%`,
        };
    };

    const handleChange = (e) => {

        setFormValues({
            ...formValues,
            [e.target.name]: parseInt(e.target.value),
        });
    }

    const handleCheck = (e) => {

        setFormValues({
            ...formValues,
            [e.target.name]: e.target.checked,
        });
    }


    return (
        <div className="calculator-wrapper">


            <div className="calculator-body">
                <h3 className="calculator-field-label">
                    Number Of Units
                </h3>
                <div className="calculator-body-number-of-units-component">

                    <input type="range" id="numOfUnits" name="numOfUnits" min="1" max="5000"
                           value={formValues.numOfUnits} onChange={(e) => handleChange(e)} style={getBackgroundSize()}/>
                    <input type="number" id="numOfUnits" name="numOfUnits" className="numberInputWithScroll" min="1"
                           max="5000" value={formValues.numOfUnits} onChange={(e) =>
                        e.target.value <= 5000 ? handleChange(e) : ""
                    }/>
                </div>
                <br/>

                <div className="monthly-yearly-check">
                    <span className="monthly-yearly-check-label">Monthly</span><IOSSwitch name="monthlyYearlyCheck"
                                                                                          checked={formValues.monthlyYearlyCheck}
                                                                                          onChange={handleCheck}/><span
                    className="monthly-yearly-check-label">Yearly</span>
                </div>

                <br/><br/>

                <div className="calculator-two-column-layout left-right-padding">
                    <TextFieldWithLabel label="Websites" name="numOfWebsites" adornment="Website(s)"
                                        adornmentPosition="end" handleChange={handleChange} formValues={formValues}/>
                    <TextFieldWithLabel label="Merchant Applications" name="merchantApplications"
                                        adornment="Application(s)" adornmentPosition="end" handleChange={handleChange}
                                        formValues={formValues}/>
                    <TextFieldWithLabel label="Monthly eSignatures" name="eSignatures" adornment="eSignature(s)"
                                        adornmentPosition="end" handleChange={handleChange} formValues={formValues}/>
                    <TextFieldWithLabel label="Annual Onboardings" name="annualOnboardings" adornment="Onboarding(s)"
                                        adornmentPosition="end" handleChange={handleChange} formValues={formValues}/>
                </div>
                <br/>
                <br/>
                <PlansTabsComponent formValues={formValues}/>
            </div>


        </div>
    );
}

export default PricingComparison;