import React, {useEffect, useState} from 'react';
import "./QuizQuestionStyles.css"
import QuizQuestionAnswerChoice from "./QuizQuestionAnswerChoice/QuizQuestionAnswerChoice";
import ContinueButton from "../ContinueButton/ContinueButton";

function QuizQuestion({question, activeQuestionNumber, setActionQuestionNumber, setSelectedAnswers, selectedAnswers}) {

    const [activeAnswer, setActiveAnswer] = useState("")

    useEffect(() => {
        if(document.getElementsByClassName("activeQuizQuestion").length){
            document.getElementsByClassName("activeQuizQuestion")[0].classList.remove("activeQuizQuestion")
            document.getElementById("continue-button-id").classList.remove("continue-button-active")
        }

        if(activeAnswer){
            document.getElementById(activeAnswer).classList.add("activeQuizQuestion")
            document.getElementById("continue-button-id").classList.add("continue-button-active")
        }

    }, [activeAnswer])



    return (
        <div className="quizQuestionWrapper">
            <h3 className="quizQuestionTitle">{question.questionText}</h3>
            {question.questionAnswers.map((q, i) => {
                return <QuizQuestionAnswerChoice answer={q} adornment={String.fromCharCode(65 + i)} setActiveAnswer={setActiveAnswer} answerNumber={i} id={"questionAnswer" + i}/>
            })}
            <ContinueButton setActionQuestionNumber={setActionQuestionNumber} activeQuestionNumber={activeQuestionNumber} activeAnswer={activeAnswer} setActiveAnswer={setActiveAnswer} question={question} setSelectedAnswers={setSelectedAnswers} selectedAnswers={selectedAnswers}/>
        </div>

    );
}

export default QuizQuestion;